var EventEmitter = require('events').EventEmitter;
var assign = require('object-assign');
var https = require('https');

var NavConstants = require('./reader-constants');
var AppDispatcher = require('../dispatcher/app-dispatcher');

const CHANGE_EVENT = 'nav_change';

var _state = {
    navigationIsOpen: true,
    toc: null,
    html: null
};

var TocStore = assign({}, EventEmitter.prototype, {

    /**
     * Get the entire collection of results.
     */
    getState: function () {
        return _state;
    },

    emitChange: function () {
        this.emit(CHANGE_EVENT);
    },

    /**
     * @param {function} callback
     */
    addChangeListener: function (callback) {
        this.on(CHANGE_EVENT, callback);
    },

    /**
     * @param {function} callback
     */
    removeChangeListener: function (callback) {
        this.removeListener(CHANGE_EVENT, callback);
    }
});

// Register callback to handle all updates
AppDispatcher.register(function (action) {
    switch (action.actionType) {
        case NavConstants.NAV_CLOSE:
            _state.navigationIsOpen = false;
            TocStore.emitChange();
            break;
        case NavConstants.NAV_TOGGLE:
            //console.log(_state.navigationIsOpen +" = "+!_state.navigationIsOpen);
            _state.navigationIsOpen = !_state.navigationIsOpen;
            TocStore.emitChange();
            break;
        default:
        // no op
    }
});

module.exports = TocStore;
