const React = require('react');
const _ = require('underscore');
const PureRenderMixin = require('react-addons-pure-render-mixin');

const ToCEntry = require('./toc-entry.jsx');

const ReaderConstants = require('./reader-constants');

function createEntries(items) {
    return _.map(items, function (item, i) {
        const hasChildren = item.children &&
            item.children.length > 0;
        const children = hasChildren ?
            <ol>{createEntries(item.children)}</ol>
            : '';

        return <ToCEntry
            key={i}
            item={item}>
            {children}
        </ToCEntry>
    });
}

const ToC = React.createClass({
    mixins: [PureRenderMixin],

    propTypes: {
        //toc: React.PropTypes.array.isRequired,
        isOpen: React.PropTypes.bool.isRequired
    },

    componentDidUpdate: function (prevProps, prevState) {
        console.log("ToC updated");
    },

    render: function () {
        var items = this.props.toc;
        var navStyle = {
            padding: '0',
            margin: '0',
            backgroundColor: '#fff',
            width: ReaderConstants.navWidth + 'px',
            position: 'fixed',
            top: '0',
            left: this.props.isOpen ? 0 : "-" + ReaderConstants.navWidth + "px",
            bottom: '0',
            zIndex: '2000',
            overflowY: 'auto'
        };

        if (items) {
            return <div id="drawer" className={this.props.isOpen?"nav-shown":"nav-hidden"}>
                <div style={navStyle} id="navigation-menu">
                    <nav id="document-navigation">
                        <ol>
                            {createEntries(items)}
                        </ol>
                    </nav>
                </div>
            </div>
        } else {
            return <nav id="toc"/>
        }
    }
});

module.exports = ToC;