var AppDispatcher = require('../dispatcher/app-dispatcher');
var NavConstants = require('./reader-constants');

var NavActions = {
    close: function () {
        AppDispatcher.dispatch({
            actionType: NavConstants.NAV_CLOSE
        });
    },
    toggle: function () {
        AppDispatcher.dispatch({
            actionType: NavConstants.NAV_TOGGLE
        });
        //console.log("NavToggle action dispatched");
    },
    getHtml: function (bwbId) {
        AppDispatcher.dispatch({
            actionType: NavConstants.LOAD_HTML,
            bwbId: bwbId
        });
    }
};

module.exports = NavActions;
