const React = require('react');
const _ = require('underscore');
const ReactDOM = require('react-dom');
const PureRenderMixin = require('react-addons-pure-render-mixin');

const Entry = React.createClass({
    mixins: [PureRenderMixin],

    propTypes: {
        item: React.PropTypes.object.isRequired
    },

    _toggleOpen: function (e) {
        e.preventDefault();
        this.setState({
            isOpen: !this.state.isOpen
        });
    },

    componentDidUpdate: function (prevProps, prevState) {
        console.log("ToC entry updated");
    },

    getInitialState: function () {
        return {
            isOpen: false
        };
    },

    render: function () {

        const i = this.props.item;

        var hasChildren = i.children && i.children.length > 0;
        var sublabel = i.sublabel ? <div className="sublabel">{i.sublabel}</div> : '';
        var range = '';
        if (i.range && i.range.length > 0) {
            if (i.range.length > 1) {
                range = <div className="range">{i.range[0].label+" — "+i.range[1].label}</div>;
            } else {
                range = <div className="range">{i.range[0].label}</div>;
            }
        }

        var className = "title-container labelling ";


        if (hasChildren) {
            className = className + ' sublist-labelling';
            var topClass = 'sublist' + (this.state.isOpen ? '' : ' collapsed');
            return <li className={topClass}>
                <div className={className}>
                    <a onClick={this._toggleOpen} href={'#'+i.id}>
                        <div className="title-container">
                            <div className="label">{i.label}</div>
                            {sublabel}
                            {range}
                        </div>
                    </a>
                </div>
                {this.props.children}
            </li>
        } else {
            return <li className='item'>
                <div className={className}>
                    <a href={'#'+i.id}>
                        <div className="title-container">
                            <div className="label">{i.label}</div>
                            {sublabel}
                        </div>
                    </a>
                </div>
                {this.props.children}
            </li>
        }
    }
});

module.exports = Entry;