const React = require('react');
const PureRenderMixin = require('react-addons-pure-render-mixin');

const TopBar = require('./top-bar.jsx');
const ToC = require('./toc.jsx');
const LoadingView = require('./loading.jsx');
const ErrorView = require('./error.jsx');
const DocContentView = require('./doc-content.jsx');
const MenuActions = require('../sidebar-menu/menu-actions');
const MenuStore = require('../sidebar-menu/sidebar-menu-store');

const MenuButton = require('../sidebar-menu/menu-button.jsx');
const NavigationToggle = require('./navigation-toggle.jsx');

//const FlatButton = require('material-ui/lib/flat-button');
//const RaisedButton = require('material-ui/lib/raised-button');
const FloatingActionButton = require('material-ui/lib/floating-action-button');
const FontIcon = require('material-ui/lib/font-icon');
const Colors = require('material-ui/lib/styles/colors');

const NavActions = require('./reader-actions');
const NavStore = require('./reader-store');
//const NavConstants = require('../sidebar-menu/menu-constants');

const BwbUtil = require('./bwb/util');


function getMenuState() {
    var _state = MenuStore.getState();
    return {menuIsOpen: _state.isOpen};
}

function getNavigationState() {
    var _state = NavStore.getState();
    return {navigationIsOpen: _state.navigationIsOpen};
}

const ReaderMain = React.createClass({
    mixins: [PureRenderMixin],

    propTypes: {
        bwbId: React.PropTypes.string.isRequired,
        version: React.PropTypes.string.isRequired
    },

    _renderDocContent: function () {
        return <div>
            <ToC isOpen={this.state.navigationIsOpen} toc={this.state.toc}/>
            <div id="doc-container">
                <div id="doc-positioner">
                    <div id="doc-margins">
                        <DocContentView html={this.state.htmlSnippet}/>
                    </div>
                </div>
            </div>
        </div>;
    },

    _closeNavigation: function () {
        NavActions.close();
    },
    _toggleNavigation: function () {
        NavActions.toggle();
    },

    _onNavChanged: function () {
        var navState = getNavigationState();
        //console.log("Nav state changed: " + navState);
        this.setState(navState);
    },
    _onMenuChanged: function () {
        this.setState(getMenuState());
    },
    _toggleMenu: function () {
        MenuActions.toggleMenu();
    },

    _prevWidth: 800,
    _yPos: 0,
    _onScroll: function () {
        var doc = document.documentElement;
        //var left = (window.pageXOffset || doc.scrollLeft) - (doc.clientLeft || 0);
        var top = (window.pageYOffset || doc.scrollTop) - (doc.clientTop || 0);

        if (this._yPos != top) {
            this._yPos = top;
            this.setState({yScrolled: top > 0});
        }
    },

    _onResize: function () {
        function narrow(size) {
            return 640 > size;
        }

        // Open / close navigation drawer
        var newWidth = window.innerWidth ||
            document.documentElement.clientWidth ||
            document.body.clientWidth;
        if (newWidth != this._prevWidth) {
            //console.log(this.state.navigationIsOpen + ": " + this._prevWidth +
            //    "(" + narrow(this._prevWidth) +
            //    ") => " + newWidth + " (" + narrow(newWidth) + ")");
            if (
                (this.state.navigationIsOpen && !narrow(this._prevWidth) && narrow(newWidth)) ||
                (!this.state.navigationIsOpen && !narrow(newWidth) && narrow(this._prevWidth))
            ) {
                //console.log(" -> " + !this.state.navigationIsOpen);
                this._toggleNavigation();
            }
            this._prevWidth = newWidth;
        }
    },

    getDefaultProps: function () {
        return {}
    },

    getInitialState: function () {
        return {
            navigationIsOpen: NavStore.getState().navigationIsOpen,
            menuIsOpen: MenuStore.getState().isOpen,
            loadingDoc: true,
            loadingToc: true,
            htmlSnippet: "",
            toc: [],
            yScrolled: false
        };
    },


    componentDidMount: function () {
        NavStore.addChangeListener(this._onNavChanged);
        MenuStore.addChangeListener(this._onMenuChanged);

        // Add resize listener
        this._onResize();
        window.addEventListener("resize", this._onResize);
        window.addEventListener("scroll", this._onScroll);

        // Get html
        var _this = this;
        BwbUtil.load(this.props.bwbId, this.props.version, _this);
    },

    componentWillUnmount: function () {
        NavStore.removeChangeListener(this._onNavChanged);
        MenuStore.removeChangeListener(this._onMenuChanged);
        window.removeEventListener("resize", this._onResize);
    },

    componentDidUpdate: function (prevProps, prevState) {
        console.log("Reader component updated");

        if (prevState.htmlSnippet != this.state.htmlSnippet) {
            console.log("HTML changed -> generate new ToC");

            var doc = document.querySelector('.legal-document');
            if (doc) {
                var toc = BwbUtil.generateToc(doc);
                if (toc.length < 1) console.error("Warning: ToC was empty");
                if (this.isMounted()) {
                    this.setState({
                        loadingToc: false,
                        loading: false,
                        toc: toc,
                        error: null
                    });
                }
            } else {
                throw new Error("Could not find document content.");
            }
        }
    },

    render: function () {
        //console.log("render doc view");
        //console.log(this.state);
        if (this.state.loadingDoc) {
            //console.log("render loading view");
            return <LoadingView />;
        } else if (this.state.error) {
            //console.log("render error view");
            console.error(this.state.error);
            return <ErrorView/>;
        } else {
            //console.log("render reader-main: ");
            //console.log(this.state);


            //<MenuButton
            //    isOpen={this.state.menuIsOpen}
            //    style={{
            //        right: 0/*this.state.menuIsOpen ? MenuConstants.menuWidth + 'px' : 0*/
            //    }}
            //    handleClick={this._toggleMenu}/>
            return <div>
                <TopBar
                    navigationIsOpen={this.state.navigationIsOpen}
                    menuIsOpen={this.state.menuIsOpen}
                />
                {this._renderDocContent()}

                <div id="nav-overlay"
                     onClick={this._closeNavigation}
                     className={this.state.navigationIsOpen?'visible':'hidden'}
                ></div>

                <NavigationToggle
                    smoothCorner={this.state.yScrolled}
                    isOpen={this.state.navigationIsOpen}
                    handleClick={this._toggleNavigation}
                />


                <FloatingActionButton
                    style={{
                    position: 'fixed',
                    bottom: 0,
                    right:  0,
                    margin: '16px'
                }}
                    onClick={function(){window.scrollTo(0,0);}}
                    backgroundColor="#fff"
                    mini={true}
                >
                    <FontIcon
                        color={Colors.grey900}
                        className="material-icons"
                    >expand_less</FontIcon>
                </FloatingActionButton>

            </div>;
        }
    }
});

module.exports = ReaderMain;